import React, { useState } from "react";
import { DatePicker } from "antd";
import moment from "moment";
import "./StepOne.css";
import axios from "axios";
import { useHistory } from "react-router-dom";
import ReactGA from 'react-ga';
ReactGA.initialize('UA-161437152-1');

function StepOne({ data }) {
  ReactGA.pageview(window.location.pathname + window.location.search);
  const [myDateString, setMyDateString] = useState("");
  const [taDateString, setTaDateString] = useState("");
  const history = useHistory();

  const dateFormat = "YYYY-MM-DD";
  const handleClick = () => {
    // ! 는 not 연산인데, 값이 없거나 false 일 때 true 반환, 한번더 !를 붙여주면 깔끔한 false 반환하게 됨
    if (!!myDateString && !!taDateString) {
      // 둘다 어떤 값이 있으므로 axios 실행
      axios
        .get(
          `https://his8t8n927.execute-api.ap-northeast-2.amazonaws.com/dev/day?my=${myDateString}&ta=${taDateString}`
        )
        .then(response => {
          const { data } = response;
          history.push({
            pathname: "/StepTwo",
            state: { detail: data }
          });
        });

      // response 를 받아서 스텝2로 전달해주어야 함
    } else {
      // 하나라도 빈문자열일 때 제대로 입력해달라는 경고 띄우기
      alert("두 날짜를 모두 선택해주셔야 합니다.");
    }
  };

  return (
    <div className="container">
      <br />
      <br />
      <div className="title">step 1</div>
      <br />
      <br />
      <div className="title-name">
        나와 상대방의
        <br />
        생년월일을 입력 하세요.
      </div>
      <br />
      <br />
      <div>
        <div className="title">*나의 생년월일</div>
        <br />
        <DatePicker
          defaultValue={moment("1990-09-01", dateFormat)}
          format={dateFormat}
          style={{ width: "100%", height: "56px" }}
          onChange={(d, ds) => {
            setMyDateString(ds);
          }}
        />
      </div>
      <br />
      <br />
      <div>
        <div className="title">*상대방의 생년월일</div>
        <br />
        <DatePicker
          defaultValue={moment("1990-09-01", dateFormat)}
          format={dateFormat}
          style={{ width: "100%", height: "56px" }}
          onChange={(d, ds) => {
            setTaDateString(ds);
          }}
        />
      </div>
      <br />
      <br />
      <br />
      <br />
      <div>
        <button className="confirm" onClick={handleClick}>
          확인
        </button>
      </div>
    </div>
  );
}

export default StepOne;
